* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(154,9,226,1) 47%, rgba(0,212,255,1) 100%);
  color: #ffffff;
  font-family: sans-serif;
  min-height: 100vh;
}

header {
  font-size: 2rem;
}

header,
form {
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;

}

.todo-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

form input,
form button {
  padding: 0.5rem;
  font-size: 2rem;
  border: none;
  background: #ffffff;
}


.todo-list {
  min-width: 30%;
  list-style: none;
}


.todo {
  margin: 0.5rem;
  background: #ffffff;
  font-size: 1.5rem;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 1s ease;
}



.complete-btn {
  background: rgb(11, 212, 162);
  color: #ffffff;
  border: none;
  padding: 1rem;
  cursor: pointer;
}

.todo-item {
  padding: 0rem 0.5rem;
  
}

.fa-trash,
.fa-check {
  pointer-events: none;
}


.filter-todo {
  padding: 1rem;
}

.todo li {
  flex: 1;
}

.trash-btn {
  background: #9a09e2;
  color: #ffffff;
  border: none;
  padding: 1rem;
  cursor: pointer;
}

.fall {
  transform: translateY(10rem) rotateZ(20deg);
  opacity: 0;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}
